import React from 'react';
import './serviceInfo.css';

function ServiceInfo(props) {
    const { basicPrice, premiumPrice, ultimatePrice, des_1, des_2, des_3, des_4, des_5, des_6, des_7, des_8, des_9, des_10, des_11, des_12 } = props;
    return (
        <div className='my-5 py-5 d-md-flex justify-content-center'>

            <div class="pricing-table">
                <h3 class="pricing-title">Basic</h3>
                {/* <!-- Lista de Caracteristicas / Propiedades --> */}
                <ul class="table-list">
                    <li>{des_1}</li>
                    <li>{des_2}</li>
                    <li>{des_3}</li>
                    <li>{des_4}</li>
                </ul>
                {/* <!-- Contratar / Comprar --> */}
                <div class="table-buy">
                    <p>{basicPrice}<sup>/ month</sup></p>
                    <a href="#" class="pricing-action">Add to cart</a>
                </div>
            </div>

            <div class="pricing-table recommended">
                <h3 class="pricing-title">Premium</h3>
                {/* <!-- Lista de Caracteristicas / Propiedades --> */}
                <ul class="table-list">
                    <li>{des_5}</li>
                    <li>{des_6}</li>
                    <li>{des_7}</li>
                    <li>{des_8}</li>
                </ul>
                {/* <!-- Contratar / Comprar --> */}
                <div class="table-buy">
                    <p>{premiumPrice}<sup>/ month</sup></p>
                    <a href="#" class="pricing-action">Add to cart</a>
                </div>
            </div>

            <div class="pricing-table">
                <h3 class="pricing-title">Ultimate</h3>
                {/* <!-- Lista de Caracteristicas / Propiedades --> */}
                <ul class="table-list">
                    <li>{des_9}</li>
                    <li>{des_10}</li>
                    <li>{des_11}</li>
                    <li>{des_12}</li>
                </ul>
                {/* <!-- Contratar / Comprar --> */}
                <div class="table-buy">
                    <p>{ultimatePrice}<sup>/ month</sup></p>
                    <a href="#" class="pricing-action">Add to cart
                    </a>
                </div>
            </div>
        </div>
    )
}

export default ServiceInfo;
