import React from 'react';
import { Link } from 'react-router-dom';
import ServiceCard from '../../components/Service_Card';
import './3_DModeling.css';

function Three_DModeling() {
    return (
        <div className='container'>
            <div className='d-flex justify-content-center flex-wrap my-5 py-5' id='graphics'>
                <Link to='/3DmodelingPricing' className='service'>
                    <ServiceCard
                        name='3D Modeling'
                    />
                </Link>
            </div>
        </div>

    )
}

export default Three_DModeling;
