import React from 'react';
import Header from '../../components/Header/Header';

import './home.css';

import PageComponent from 'D:/shobuj/src/components/PageComponent/index.js';

//images
import GraphicsImage from 'D:/shobuj/src/image/graphics-design-w.png';
import Three_D from 'D:/shobuj/src/image/website-design-and-dev-w.png';
import SocialMarketing from 'D:/shobuj/src/image/social-media-marketing-w.png';
import Video from 'D:/shobuj/src/image/video-editing-and-animation-w.png';
import Web from 'D:/shobuj/src/image/website-design-and-dev-w.png';
import AppNsoft from 'D:/shobuj/src/image/application-and-software-dev-w.png';
import Review from '../../components/Review/Review';
import Achievements from '../../components/Portfolio/Achievements';


function Home(props) {
    return (
        <>
            <Header />
            <PageComponent
                header='Graphics Design'
                url='/graphics'
                title_1='Graphics'
                img={GraphicsImage}
                id='graphics'
                className='float-md-end float-none'
            />
            <PageComponent
                header='Video Editing & Animation'
                url='/Video'
                title_1='Video Editing And Animation'
                img={Video}
                id='pageComponent'
                flexDirection='flex-md-row-reverse'
            />
            <PageComponent
                header='Web Design And Development'
                url='/web'
                title_1='Web'
                id='pageComponent'
                img={Web}
                className='float-md-end float-none'
            />
            <PageComponent
                header='Social media marketing'
                url='/socialMediaMarketing'
                title_1='social'
                id='pageComponent'
                img={SocialMarketing}
                flexDirection='flex-md-row-reverse'
            />
            <PageComponent
                header='Application and Software'
                url='/applicationAndSoftware'
                title_1='Application And Software Development'
                id='pageComponent'
                img={AppNsoft}
                className='float-md-end float-none'
            />
            <PageComponent
                header='3D Modeling'
                url='/3DModeling'
                title_1='3D Modeling'
                id='pageComponent'
                img={Three_D}
                flexDirection='flex-md-row-reverse'
            />
            <Review />
            <Achievements />
        </>
    )
}

export default Home;
