import React, { Component } from 'react';
import Nav from '../../components/Nav';
import {Link} from 'react-router-dom';
import { auth, handleUserProfile, signInWithGoogle } from '../../firebase/utils';
import './form.css';


const initialState = {
    displayName: '',
    email: '',
    password: '',
    confirmPassword: '',
    errors: []
}


class Form extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            ...initialState
        })
        this.handleSignInChange = this.handleSignInChange.bind(this)
        this.handleSignUpChange = this.handleSignUpChange.bind(this)
    }

    //  slide Functions
    slideToSignUp = () => {
        const container = document.getElementById("container");
        container.classList.add('right-panel-active')
    }
    slideToSignIn = () => {
        const container = document.getElementById("container");
        container.classList.remove('right-panel-active')
    }

    //handler functions --- SignUp
    handleSignUpChange = e => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleSignUp = async e => {
        e.preventDefault();
        const { displayName, email, password, confirmPassword } = this.state;

        if (password != confirmPassword) {
            const err = [`Password doesn't match`];
            this.setState({
                errors: err
            })
            return;
        };
        try {
            const { user } = await auth.createUserWithEmailAndPassword(email, password);

            await handleUserProfile(user, displayName);
            this.setState({
                ...initialState
            })
        } catch (error) {
            console.log(error)
        }
    }

    //handler function -- signIn
    handleSignInChange = e => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleSignIn = async e => {
        e.preventDefault();
        const { email, password } = this.state;
        try {
            await auth.signInWithEmailAndPassword(email, password);
            this.setState({
                ...initialState
            })
        } catch (error) {
            console.log(error)
        }
        console.log(this.state)
    }

    render() {
        const { slideToSignIn, slideToSignUp, handleSignUpChange, handleSignUp, handleSignInChange, handleSignIn } = this;

        const { displayName, email, password, confirmPassword } = this.state;

        return (
            <div>
                <div class='container form-group input-group mt-2' id="container">
                    <div class="form-container sign-up-container">
                        <form onSubmit={handleSignUp}>
                            <h2>Create Account</h2>
                            <span className='text-secondary'>or sign-up using your email</span>
                            <input
                                type="text" placeholder="First Name" class='form-control'
                                name='displayName'
                                value={displayName}
                                onChange={handleSignUpChange}
                                required
                            />
                            <input type="email" placeholder="Email" class='form-control'
                                name='email'
                                value={email}
                                onChange={handleSignUpChange}
                            />
                            <input type="password" name='password'
                                value={password}
                                class='form-control' placeholder="password" required minlength="8"
                                onChange={handleSignUpChange} />
                            <input type="password" name='confirmPassword'
                                value={confirmPassword}
                                class='form-control' placeholder="Confirm Password" required minlength="8"
                                onChange={handleSignUpChange} />
                            <div class="d-flex flex-row"
                                id='pass-checkbox-container'>
                                <input type="checkbox" name="checkbox" id="pass-checkbox" />
                                <p>Show password</p>
                            </div>

                            <button class='btn btn-sm btn-primary mb-2' id='signUp' onClick={handleUserProfile}>Sign up</button>
                        </form>
                    </div>
                    <div class="form-container sign-in-container">
                        <form onSubmit={handleSignIn}>
                            <h1>Sign In</h1>
                            <div class="social-container">
                                <a href="#" class="social"><i class="fa fa-facebook-f"></i></a>

                                <a href="#" class="social" onClick={signInWithGoogle}><i class="fa fa-google"></i></a>

                                <a href="#" class="social"><i class="fa fa-linkedin"></i></a>
                            </div>
                            <span>or sign-in using your email</span>
                            <input type="email" placeholder="Email" name='email' class='form-control'
                                onChange={handleSignInChange}
                                required
                            />
                            <input type="password" name='password' class='form-control' placeholder="password"
                                onChange={handleSignInChange}
                                required />
                            <div class="d-flex flex-row"
                                id='pass-checkbox-container'>
                                <input type="checkbox" name="checkbox" id="pass-checkbox" />
                                <p>Show password</p>
                            </div>
                            <Link to='/forgotPassword'>
                                    <p className='text-muted'>Forgot password?</p>
                            </Link>

                            <button class='btn btn-sm btn-primary'>Sign In</button>
                        </form>
                    </div>

                    {/* <!-- overLay --> */}

                    <div class="overlay-container">
                        <div class="overlay">
                            <div class="overlay-panel overlay-left">
                                <h1>Welcome Back!</h1>
                                <p>
                                    To keep connected with us please login.
                                </p>
                                <button class="ghost btn btn-sm text-light" id="signIn" onClick={slideToSignIn}>Sign In</button>
                            </div>

                            <div class="overlay-panel overlay-right">
                                <h1>Hello, Friend!</h1>
                                <p>
                                    Enter your personal details and start journey with us
                                </p>
                                <button class="ghost btn btn-sm text-light" id="signUp" onClick={slideToSignUp}>Sign Up</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Form;
