import React from 'react';
import { useState } from 'react';
import { withRouter } from 'react-router-dom';
import { auth } from './../../firebase/utils';

function ForgotPassword(props) {
  const [email, setEmail] = useState('');

  const handleSubmit = async e => {
    e.preventDefault();
    console.log('clicked');
    try {
      const config = {
        url: 'http://localhost:3000/form'
      }
      await auth.sendPasswordResetEmail(email, config)
        .then(() => {
          props.history.push('/form');
        })
        .catch(() => {
          console.log('error happened');
        })
    } catch (error) {
      console.log(error)
    }
  }

  return (
    <div className='container'>
      <form className='d-flex flex-column'>
        <input type='email' name='email' value={email} placeholder='Email' className='form-control my-2' onChange={e => setEmail(e.target.value)}>
        </input>
        <input type='button' className='btn btn-outline-primary' value='Send Password' onClick={handleSubmit}>
        </input>
      </form>
    </div>

  )
}

export default withRouter(ForgotPassword)
